const requireApi = require.context('.', true, /.js$/);
/*
webpackapi,
require.context,
,,
,api,
,,
import

*/
const module = {};
requireApi.keys().forEach((key) => {
	if(key === './index.js' || key === './api/request.js'){
		return;
	}
	Object.assign(module, requireApi(key));
})

export default module;