export default ({name, data= {}}) => {
	return new Promise((resolve, reject) => {
		const { noLoading = true } = data;
		
		noLoading && uni.showLoading({});
		// 
		uniCloud.callFunction({
			// 
			name,
			data,
			success : ({result}) => {
				//  
				if(result.code === 0) {
					resolve(result);
				}else{
					uni.showToast({
						icon: "none",
						title: result.msg,
					})
				}
			},
			fail: (err) => {
				reject(err);
			},
			complete(){
				// 
				noLoading && uni.hideLoading();
			}
		})
	})
}